
<template>
  <dashboard-template>
    <section class="py-5">
      <div class="container">
        <div class="row">
          <div class="col-md-12">
            <h3>Post Feedback & Ratings</h3>
            <div>
              <label for="">Give Rating</label>
              <el-rate v-model="feedback.rating"></el-rate>
              <div class="col-md-12 py-3">
                <label class="form-label fw-semibold">Send Feedback</label>
                <textarea
                  name=""
                  id=""
                  class="form-control"
                  rows="5"
                  v-model="feedback.message"
                ></textarea>
              </div>
              <button
                type="button"
                class="btn btn-secondary my-3"
                @click="postFeedback()"
              >
                Submit
              </button>
            </div>
          </div>
        </div>
      </div>
    </section>
  </dashboard-template>
</template>

<script>
import DashboardTemplate from "@/views/DashboardTemplate.vue";
import { axiosApi } from "@/config/axios";

export default {
  components: { DashboardTemplate },
  data() {
    return {
      feedback: {
        rating: null,
        message: "",
      },
    };
  },
  methods: {
    async postFeedback() {
      try {
        var user = JSON.parse(localStorage.getItem("user"));
        if (user != null) {
          var token = user.accessToken;
          console.warn(this.feedback);
          axiosApi.defaults.headers.common["Authorization"] = `Bearer ${token}`;
        }
        var response = await axiosApi.post("feedbackapi", this.feedback, {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
        });
        if (response.status == 200) {
          alert(response.data.message);
        }
        console.warn(response.data);
        window.location.reload();
      } catch (e) {
        console.warn(e.message);
      }
    },
  },
};
</script>

