<template>
  <dashboard-template>
    <section>
      <div class="container">
        <h1>User Profile</h1>
        <div>
          <div class="row">
            <div class="col-md-2">
              <img :src="profile.photo" class="img-fluid rounded" alt="" />
            </div>

            <div class="col-md-12"></div>
            <div class="col-md-6">
              <div class="mb-3">
                <label for="" class="form-label">First Name</label>
                <input
                  type="text"
                  class="form-control"
                  v-model="profile.first_name"
                />
              </div>
            </div>

            <div class="col-md-6">
              <div class="mb-3">
                <label for="" class="form-label">Last Name</label>
                <input
                  type="text"
                  class="form-control"
                  v-model="profile.last_name"
                />
              </div>
            </div>

            <div class="col-md-6">
              <div class="mb-3">
                <label for="" class="form-label">Address</label>
                <input
                  type="text"
                  class="form-control"
                  v-model="profile.address"
                />
              </div>
            </div>

            <div class="col-md-6">
              <div class="mb-3">
                <label for="" class="form-label">Contact</label>
                <input
                  type="text"
                  class="form-control"
                  v-model="profile.contact"
                />
              </div>
            </div>

            <div class="col-md-4">
              <div class="mb-3">
                <label for="" class="form-label">Room</label>
                <input
                  type="text"
                  class="form-control"
                  v-model="profile.room"
                  disabled
                />
              </div>
            </div>

            <div class="col-md-4">
              <div class="mb-3">
                <label for="" class="form-label">Bed No</label>
                <input
                  type="text"
                  class="form-control"
                  v-model="profile.bed"
                  disabled
                />
              </div>
            </div>

            <div class="col-md-4">
              <div class="mb-3">
                <label for="" class="form-label">Status</label>
                <input
                  type="text"
                  class="form-control"
                  v-model="profile.status"
                  disabled
                />
              </div>
            </div>
          </div>
          <button
                class="btn btn-primary"
                @click.prevent="updateUser(profile)"
              >
                Update Profile
              </button>
        </div>

        <div class="py-5">
          <h4>Change Password</h4>
          <div class="row">
            <div class="col-md-6">
              <div class="mb-3">
                <label for="" class="form-label">Email</label>
                <input
                  type="text"
                  class="form-control"
                  v-model="profile.email"
                />
              </div>
            </div>
            <div class="col-md-6">
              <div class="mb-3">
                <label for="" class="form-label">New Password</label>
                <input
                  type="password"
                  class="form-control"
                  v-model="user.password"
                />
              </div>
            </div>
          </div>
          <button
                @click.prevent="changePassword(user)"
                class="btn btn-success"
              >
                Change Password
              </button>
        </div>
      </div>
    </section>
  </dashboard-template>
</template>

<script>
import DashboardTemplate from "@/views/DashboardTemplate.vue";
import { mapActions, mapGetters } from "vuex";

// import CarouselComponent from "@/components/CarouselComponent.vue";
export default {
  components: { DashboardTemplate },

  data() {
    return {
      user: {
        password: "",
      },
    };
  },
  methods: {
    ...mapActions(["updateUser", "changePassword"]),
  },
  computed: {
    ...mapGetters({
      profile: "get_profile",
    }),
  },
};
</script>

<style scoped>
</style>
