<template>
  <template-view>
    <div>
      <div class="row">
        <div class="address col-md-6">

          <h3 class="pt-3">Location</h3>
          <p>{{ company.address }}</p>
          <h5>{{ company.name }}</h5>
          <a href="">Phone: {{ company.contact }}</a> <br />
          <a href="">Email: {{ company.email }}</a>
          <div class="py-5">
            <p>Follow Us on Social Media</p>
            <i class="fa-brands fa-square-facebook px-3 fa-2xl"></i>
            <i class="fa-brands fa-instagram fa-2xl"></i>
            <i class="fa-brands fa-twitter fa-2xl px-3"></i>

          </div>
        </div>
        <div class="col-md-6">
          <form>
          <h3>Message Us</h3>
          <div class="mb-3">
            <label class="form-label">Full Name</label>
            <input
              type="text"
              class="form-control"
              placeholder="Alisha Limbu"
              v-model="user.name"
            />
          </div>
          <div class="mb-3">
            <label class="form-label">Email</label>
            <input
              type="email"
              class="form-control"
              placeholder="yourname@.com"
              v-model="user.email"
            />
          </div>
          <div class="mb-3">
            <label class="form-label">Discription</label>
            <textarea
              style="border-radius: 12px"
              class="form-control"
              rows="5"
              placeholder="Leave us message here"
              v-model="user.description"
            ></textarea>
          </div>

          <button type="submit" class="btn btn-secondary" @click="message">Submit</button>
        </form>
        </div>
      </div>
      <div class="py-5">
        <h2>Find Us on Google Maps</h2>
        <iframe
          src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d14263.134620967507!2d87.3019478!3d26.6554085!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x2959e2a3e2bf54e0!2sItahari%20International%20College!5e0!3m2!1sen!2snp!4v1672194847542!5m2!1sen!2snp"
          style="border: 0; height: 100vh; width: 100%"
          allowfullscreen=""
          loading="lazy"
          referrerpolicy="no-referrer-when-downgrade"
        ></iframe>
      </div>
    </div>
  </template-view>
</template>

<script>
import TemplateView from "@/views/TemplateView.vue";
import { mapGetters } from 'vuex';
export default {
  components: { TemplateView },
  data(){
    return{
      user: {
        name: "",
        email: "",
        description: "",
      }
    }
  },
  methods:{
    message(e){
      this.axios.post("http://127.0.0.1:8000/api/messageapi/",this.user)
      .then((result)=>{
        console.warn(result)
        // window.location.reload()
        console.warn(this.user)
    })
      e.preventDefault();
    }
  },

  computed:{
    ...mapGetters({
      company:"get_company",
    })
  }
};
</script>

<style scoped>
.address {
  background-image: url("https://static.wixstatic.com/media/20e5671799c242d8b8faf5edc41c7311.jpg/v1/fill/w_673,h_763,al_c,q_90,enc_auto/20e5671799c242d8b8faf5edc41c7311.jpg");
  overflow: hidden;
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
}

a {
  text-decoration: none;
  color: black;
  font-size: 18px;
    font-family: Times New Roman 
}
p {
    font-size: 18px;
    font-family: Times New Roman  }
</style>