<template>
  <template-view>
    <section class="bgimage">
      <div class="container">
        <div class="row">
          <div class="col-md-4 offset-md-4">
            <div class="login-container">
              <div class="login">
                <div class="card w-100 p-5">
                  <h3 class="text-center">Member Portal</h3>
                  <form>
                    <div class="form-group">
                      <label for="my-input">Email</label>
                      <input
                        id="my-input"
                        class="form-control"
                        type="text"
                        name=""
                        placeholder="Email"
                        v-model="student.email"
                      />
                    </div>

                    <div class="form-group my-2">
                      <label for="my-input">Password</label>
                      <input
                        id="my-input"
                        class="form-control"
                        type="password"
                        name=""
                        placeholder="Password"
                        v-model="student.password"
                      />
                    </div>

                    <!-- <div class="form-group my-2 text-center">
                       <span>Don't have an account? </span> <a class="text-center" href="" @click.prevent="$router.push({name:'register'})">Click here</a>
                  </div> -->

                    <div class="form-group my-2 text-center">
                      <button class="btn btn-primary" @click.prevent="login()">
                        Login
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </template-view>
</template>

<script>
import { axiosApi } from "@/config/axios";
import TemplateView from "./TemplateView.vue";
import router from "@/router";
export default {
  components: { TemplateView },
  name: "LoginView",
  computed: {
    studentLogin() {
      return JSON.parse(localStorage.getItem("user"));
    },
  },
  mounted() {
    if (this.studentLogin) {
      this.$router.push({ name: "dashboard" });
    }
  },
  data() {
    return {
      student: {
        email: "",
        password: "",
      },
    };
  },
  methods: {
    async login() {
      var response = await axiosApi.post("user/login", this.student);
      if (response.data.message == "login successfull") {
        localStorage.setItem("user", JSON.stringify(response.data));
        router.push({ name: "dashboard" });
      } else {
        alert(response.data.message);
      }
    },
  },
};
</script>

<style scoped>
.login {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 70vh;
}
.bgimage {
  width: 100%;
  height: 100vh;
  background-image: url("@/assets/1.png");
  object-fit: cover;
  background-size: cover;
}
</style>
