<template>
  <template-view>
    <div class="py-5">
      <h5>Review Feedback</h5>
      <div class="container">
        <div class="row">
          <div class="col-md-3" v-for="(data, index) in feedback" :key="index">
            <div class="card d-flex py-4">
              <div>
                <img
                  :src="data.image"
                  width="100"
                  height="100"
                  style="border: 1px solif red; border-radius: 100%;objectFit:cover"
                  alt=""
                />
                <div class="my-2">{{ data.firstName }} {{ data.lastName }}</div>
              </div>
              <div>
                <div>{{ data.message }}</div>
                <el-rate
                  v-model="data.rating"
                  disabled
                  show-score
                  text-color="#ff9900"
                  score-template="{value} points"
                >
                </el-rate>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </template-view>
</template>

<script>
import TemplateView from "@/views/TemplateView.vue";
import { mapGetters } from "vuex";
export default {
  components: { TemplateView },

  computed: {
    ...mapGetters({
      feedback: "get_feedback",
    }),
  },
};
</script>

