      <section class="d-flex" style="height: 150vh">
        <div class="bg-secondary w-25 pt-5">
          <h3 class="text-info ">Student Dashboard</h3>
          <ul style="list-style-type: none">
            <li class="my-3">
              <a href="" @click.prevent="$router.push({ name: 'home' })"
                >Home Page</a
              >
            </li>
            <li class="my-3">
              <a href="" @click.prevent="$router.push({ name: 'dashboard' })"
                >Dashboard</a
              >
            </li>

            <li class="my-3">
              <a
                href=""
                @click.prevent="
                  fetchProfile().then(() => $router.push({ name: 'profile' }))
