              >
            </li>
            <li class="my-3">
              <a href="" @click.prevent="$router.push({ name: 'message' })"
                >Message</a
              >
            </li>
            <li class="my-3"><a href="" @click="logOut()">Logout</a></li>
          </ul>
        </div>
        <div class="w-75 pt-5">
          <slot></slot>
        </div>
      </section>
    </main>

    <footer></footer>
  </div>
</template>

<script>
import { mapActions } from "vuex";
// import StudentNavbar from "@/components/StudentNavbar.vue";
export default {
  name: "DashboardTemplate",
  //   components: { StudentNavbar },
  methods: {
    ...mapActions(["fetchProfile"]),
    logOut() {
      localStorage.removeItem("user");
      this.$router.push({ name: "home" });
    },
  },
};
</script>

