<template>
  <dashboard-template>
    <!-- Carousel -->
    <!-- <carousel-component /> -->
    <h1>Dashboard</h1>
    <img :src="profile.photo" width="200" height="300" class="rounded" alt="" />
    <div class="my-2">
      <span class="fw-bold">Welcome</span> {{ profile.first_name }}
      {{ profile.last_name }} | <span class="fw-bold">Email:</span>
      {{ profile.email }}
    </div>
    <div class="container py-5">
      <div class="row">
        <div class="col-md-4 offset-2">
          <div class="card">
            <div class="card-header">
              <h5>Room Details</h5>
            </div>
            <div class="card-body">
              <h6>{{ profile.room }}</h6>
            </div>
          </div>
        </div>

        <div class="col-md-4">
          <div class="card">
            <div class="card-header">
              <h5>Bed</h5>
            </div>
            <div class="card-body">
              <h6>{{ profile.bed }}</h6>
            </div>
          </div>
        </div>
      </div>
    </div>
  </dashboard-template>
</template>

<script>
import DashboardTemplate from "@/views/DashboardTemplate.vue";
import { mapActions, mapGetters } from "vuex";
// import CarouselComponent from "@/components/CarouselComponent.vue";
export default {
  components: { DashboardTemplate },
  methods: {
    ...mapActions(["fetchProfile"]),
  },
  created() {
    this.fetchProfile();
  },
  computed: {
    ...mapGetters({
      profile: "get_profile",
    }),
  },
};
</script>

<style scoped>
span{
  font-size: 18px;
    font-family: Times New Roman
}</style>
