<template>
  <dashboard-template>
    <div>
      <div class="container py-3">
        <h1>Message To Admin</h1>
        <div class="row">
          <div class="col-md-12">
            <div class="col-md-12 py-3">
              <label class="form-label fw-semibold">Send Message</label>
              <textarea
                style="border-radius: 12px"
                class="form-control"
                rows="5"
                v-model="student.description"
                placeholder="eg. i don't want to eat today's dinner"
              ></textarea>
            </div>
          </div>
        </div>
        <button type="submit" class="btn btn-secondary my-3" @click="message">
          Submit
        </button>
      </div>
    </div>
  </dashboard-template>
</template>

<script>
import { axiosApi } from "@/config/axios";
import DashboardTemplate from "./DashboardTemplate.vue";
export default {
  components: { DashboardTemplate },
  name: "StudentmessageView",
  data() {
    return {
      student: {
        name: "",
        description: "",
      },
    };
  },
  methods: {
    async message() {
      try {
        var user = JSON.parse(localStorage.getItem("user"));
        if (user != null) {
          var token = user.accessToken;
          axiosApi.defaults.headers.common["Authorization"] = `Bearer ${token}`;
        }
        var response = await axiosApi.post("studentmessageapi", this.student, {
          headers: {
            Accept: "application/json",
            Authorization: `Bearer ${token}`,
          },
        });
        console.warn(response.data);
        if (response.status == 200) {
          alert(response.data.message);
        }
        window.location.reload();
      } catch (e) {
        console.warn(e.message);
      }
    },
  },
};
</script>

