<template>
  <template-view>
    <div class="container">
      <div class="row py-5">
        <h3 class="text-center">Choose a bedroom suitable for you.</h3>
        <!-- <div class="col-md-3"></div> -->
        <div class="col-md-12">
          <div class="card my-5 p-3" v-for="(room, index) in room" :key="index">
            <div class="row">
              <div class="col-md-5">
                <img
                  :src="room.image"
                  alt=""
                  style="height: 300px; object-fit: cover"
                  class="img-fluid"
                />
              </div>
              <div class="col-md-5">
                <h3 class="py-3">{{ room.name }}</h3>
                <div v-html="room.description"></div>
              </div>
              <div class="col-md-2 my-3">
                <h5 class="py-3">Select Bed</h5>
                <div
                  class="form-check fw-4"
                  v-for="(bed, index) in room.beds"
                  :key="index"
                >
                  <input
                    class="form-check-input"
                    type="radio"
                    name="exampleRadios"
                    id="exampleRadios1"
                    disabled
                    v-if="bed.pivot.status == 'not available'"
                  />
                  <input
                    class="form-check-input text-bold"
                    type="radio"
                    name="exampleRadios"
                    id="exampleRadios1"
                    checked
                    :value="bed.id"
                    v-model="booking.bed"
                    v-else
                  />
                  <label class="form-check-label fw-4" for="exampleRadios1">
                    {{ bed.name }}
                    <span
                      :class="
                        bed.pivot.status == 'available'
                          ? 'badge bg-success'
                          : 'badge bg-secondary'
                      "
                      >{{ bed.pivot.status }}</span
                    >
                  </label>
                  <a
                    v-if="bed.pivot.status == 'available' && booking.bed != ''"
                    type="button"
                    class="badge bg-primary text-decoration-none ms-2"
                    data-bs-toggle="modal"
                    data-bs-target="#staticBackdrop"
                    @click.prevent="
                      setPrice(room.price, room.id, room.name, bed.id)
                    "
                  >
                    <i class="fa-solid fa-plus"></i>
                  </a>
                </div>

                <div class="text-end p-3">
                  <h6>Price: Rs {{ room.price }}</h6>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- Modal -->
      <div
        class="modal fade modal-lg"
        id="staticBackdrop"
        data-bs-backdrop="static"
        data-bs-keyboard="false"
        tabindex="-1"
        aria-labelledby="staticBackdropLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h1 class="modal-title fs-5" id="staticBackdropLabel">
                Please Fill the Form
              </h1>
              <button
                type="button"
                class="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div class="modal-body">
              <form @submit="book" method="post">
                <div class="row m-3">
                  <div class="col-md-6 p-3">
                    <div class="form-group">
                      <label
                        >Full Name <span class="text-danger">*</span></label
                      >
                      <input
                        id="name"
                        class="form-control"
                        type="text"
                        name="name"
                        v-model="booking.name"
                        placeholder="Alisha Limbu"
                      />
                    </div>
                  </div>
                  <div class="col-md-6 p-3">
                    <div class="form-group">
                      <label>Address <span class="text-danger">*</span></label>
                      <input
                        id="address"
                        class="form-control"
                        type="text"
                        name="address"
                        v-model="booking.address"
                        placeholder="Itahari"
                      />
                    </div>
                  </div>
                  <div class="col-md-6 p-3">
                    <div class="form-group">
                      <label>Mobile <span class="text-danger">*</span></label>
                      <input
                        id="contact"
                        class="form-control"
                        type="number"
                        name="contact"
                        v-model="booking.contact"
                        placeholder="eg. 98xxxxxxxx"
                      />
                    </div>
                  </div>
                  <div class="col-md-6 p-3">
                    <div class="form-group">
                      <label>Email <span class="text-danger">*</span></label>
                      <input
                        id="email"
                        class="form-control"
                        type="email"
                        name="email"
                        v-model="booking.email"
                        placeholder="example@.gmail.com"
                      />
                    </div>
                  </div>
                  <div class="col-md-6 p-3">
                    <div class="form-group">
                      <label>Room no. <span class="text-danger">*</span></label>
                      <select
                        class="form-select"
                        aria-label="Default select example"
                        v-model="booking.room"
                      >
                        <option
                          :value="room.id"
                          v-for="(room, index) in room"
                          :key="index"
                        >
                          {{ room.name }}
                        </option>
                      </select>
                    </div>
                  </div>
                  <div class="col-md-6 p-3">
                    <div class="form-group">
                      <label>Bed no. <span class="text-danger">*</span></label>
                      <input
                        id="bed"
                        class="form-control"
                        type="text"
                        name="bed"
                        v-model="booking.bed"
                      />
                    </div>
                  </div>
                </div>
              </form>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Close
              </button>
              <button
                data-bs-dismiss="modal"
                type="submit"
                class="btn btn-primary"
                @click="openCheckout()"
              >
                Submit
              </button>
              <!-- <button @click.prevent="openCheckout()">Pay with Khalti</button> -->
            </div>
          </div>
        </div>
      </div>
    </div>
  </template-view>
</template>

<script>
import TemplateView from "@/views/TemplateView.vue";
import { mapGetters, mapActions } from "vuex";
import KhaltiCheckout from "khalti-checkout-web";
import axios from "axios";
export default {
  components: { TemplateView },
  data() {
    return {
      booking: {
        name: "",
        address: "",
        contact: "",
        email: "",
        room: "",
        bed: "",
      },

      checkout: null,
      paymentData: {
        amount: 0,
        productIdentity: "product_id",
        productName: "Product Name",
        productUrl: "http://example.com",
        additionalData: {},
      },
    };
  },

  mounted() {
    const config = {
      publicKey: "test_public_key_7d4b38abee04406887816670b9d7c8e3",
      productIdentity: this.paymentData.productIdentity,
      productName: this.paymentData.productName,
      productUrl: this.paymentData.productUrl,
      amount: this.paymentData.amount,
      eventHandler: {
        onSuccess: (payload) => {
          // Handle successful payment
          console.log(payload);
          this.verifyPayment(payload.token, payload.amount).then(() =>
            alert("You booking has been comfirmed")
          );
        },
        onError: (error) => {
          // Handle payment error
          console.error(error);
        },
        onClose: () => {
          // Handle payment window closed
          console.log("Payment window closed");
        },
      },
    };
    this.checkout = new KhaltiCheckout(config);
  },
  methods: {
    ...mapActions(["fetchRoom"]),

    book(e) {
      const loading = this.$vs.loading({
        text: "Please wait...",
      });
      this.axios
        .post("http://127.0.0.1:8000/api/bookingapi/", this.booking, {
          headers: { "Content-type": "application/json" },
        })
        .then((result) => {
          console.warn(result);
          loading.close();
          this.fetchRoom();
        });
      e.preventDefault();
    },

    setPrice(price, roomId, bedId) {
      this.paymentData.amount = price * 100;
      this.booking.room = roomId;
      this.bed = bedId;
    },

    // Khalti Payment
    openCheckout() {
      this.checkout.show({ amount: this.paymentData.amount });
    },

    async verifyPayment(token, amount) {
      const data = { token: token, amount: amount };
      await axios
        .post("http://127.0.0.1:8000/api/khalti/payment/verify", data)
        .then((response) => {
          // Handle successful response
          console.log(response.data);
          this.book();
        })
        .catch((error) => {
          // Handle error
          console.error(error);
        });
    },
  },

  computed: {
    ...mapGetters({
      room: "get_room",
    }),
  },
};
</script>

<style scoped>
</style>